/*
 * Main navbar settings
 */

#global-navbar-container {
  background-color: #273038;
  margin-bottom: 0;
  flex: none;
}

#th-global-navbar-top {
  padding-left: 0;
  padding-top: 1px;
  border-bottom: 1px solid black;
  justify-content: space-between;
  display: flex;
  width: 100%;
  background-color: #222;
}

.navbar {
  padding: 0;
}

#th-global-navbar-top .dropdown-menu {
  top: inherit;
}

.dropdown-menu {
  min-width: inherit;
}

.dropdown-menu > li > a,
.dropdown-menu a {
  font-weight: normal;
  line-height: 1.42857143;
  color: #333;
  white-space: nowrap;
}

.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:focus {
  color: #262626;
  text-decoration: none;
}

label.dropdown-item {
  margin-bottom: 0;
}

label.dropdown-item.active,
.dropdown-item:active {
  color: #0b0c0e;
}

.dropdown-item input[type='checkbox'] {
  margin-right: 5px;
}

#th-logo {
  padding-left: 14px;
  border-left: 0;
}

#th-logo img {
  margin-top: -8px;
  max-height: 18px;
}

/*
 * Right hand upper navbar
 */

.navbar-right {
  display: flex;
  justify-content: flex-end;
}

.nav-menu-btn {
  margin-right: -4px;
  padding-left: 14px;
  padding-right: 14px;
  font-size: 13px;
}

.nav-menu-btn.dropdown-toggle {
  width: 98%;
}

.nav-menu-btn:after {
  font-size: 14px;
}

.nav-help-btn {
  margin-right: -4px;
  padding-left: 12px;
  padding-right: 8px;
}

.th-context-navbar {
  background-color: #354048;
  overflow: visible;
}

/*
 * Left hand lower navbar
 */

secondary-nav-bar {
  width: 100%;
}

.watched-repo-main-btn {
  border-right: 0;
  padding-right: 5px;
}

.watched-repo-main-btn:visited,
.watched-repo-main-btn:link {
  color: lightgrey;
}

.watched-repo-navbar {
  overflow: visible;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  display: flex;
  background-color: #354048;
}

.tree-closed {
  color: rgb(161, 52, 53);
}

.tree-open {
  color: green;
}

.tree-approval {
  color: #fb9910;
}

.tree-unavailable {
  color: lightgray;
}

/*
