 */

.group-state-nav-icon {
  width: 7px;
  display: inline-block;
}

.btn-nav-filter {
  padding: 7px 2px;
}

#filter-chicklets .btn {
  padding: 0;
  margin: 0px 1px 2px 1px;
}

.btn.btn-view-nav.btn-orange-filter-chicklet,
.btn.btn-view-nav.btn-orange-filter-chicklet:hover {
